'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from './ui/button';
import { Avatar, AvatarImage, AvatarFallback } from './ui/avatar';
import { DropdownMenu, DropdownMenuTrigger, DropdownMenuContent, DropdownMenuItem, DropdownMenuSeparator } from './ui/dropdown-menu';

const Navbar = () => {
  const { user, logout, isAuthenticated, isAdmin } = useAuth();
  const router = useRouter();
  const pathname = usePathname();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [mounted, setMounted] = useState(false);

  React.useEffect(() => {
    setMounted(true);
  }, []);

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const isActive = (path: string) => pathname === path;

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 bg-white border-b border-gray-100 shadow-sm">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="group">
            <img 
              src="/jobslly-logo.png" 
              alt="Jobslly Logo" 
              className="h-16 w-auto object-contain group-hover:scale-105 transition-transform"
            />
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            <Link 
              href="/" 
              className={`nav-link ${isActive('/') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
            >
              <span className="relative z-10">Home</span>
              <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
            </Link>
            
            <Link 
              href="/jobs" 
              className={`nav-link ${isActive('/jobs') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
            >
              <span className="relative z-10">Opportunities</span>
              <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
            </Link>
            
            <Link 
              href="/blog" 
              className={`nav-link ${isActive('/blog') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
            >
              <span className="relative z-10">Health Hub</span>
              <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
            </Link>
            
            <Link 
              href="/contact-us" 
              className={`nav-link ${isActive('/contact-us') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
            >
              <span className="relative z-10">Contact</span>
              <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
            </Link>
            
            {mounted && isAuthenticated ? (
              <div className="flex items-center space-x-4">
                <Link 
                  href="/dashboard" 
                  className={`nav-link ${isActive('/dashboard') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
                >
                  <span className="relative z-10">Dashboard</span>
                  <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
                </Link>
                
                {isAdmin && (
                  <Link 
                    href="/admin" 
                    className={`nav-link ${isActive('/admin') ? 'active' : ''} relative group font-medium text-gray-700 hover:text-teal-600 transition-colors`}
                  >
                    CMS Admin
                    <div className="absolute inset-x-0 -bottom-2 h-0.5 bg-gradient-to-r from-cyan-400 to-purple-600 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
                  </Link>
                )}

                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" className="relative h-10 w-10 rounded-full">
                      <Avatar className="h-10 w-10">
                        <AvatarImage src={user?.avatar} alt={user?.full_name || ""} />
                        <AvatarFallback className="bg-teal-100 text-teal-700">
                          {user?.full_name?.charAt(0).toUpperCase() || 'U'}
                        </AvatarFallback>
                      </Avatar>
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end" className="w-56">
                    <div className="px-2 py-1.5">
                      <p className="text-sm font-medium">{user?.full_name}</p>
                      <p className="text-xs text-gray-500">{user?.email}</p>
                    </div>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem onClick={() => router.push('/dashboard')}>
                      Dashboard
                    </DropdownMenuItem>
                    <DropdownMenuItem onClick={() => router.push('/profile')}>
                      Profile Settings
                    </DropdownMenuItem>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem onClick={handleLogout} className="text-red-600">
                      Logout
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </div>
            ) : mounted ? (
              <div className="flex items-center space-x-3">
                <Button 
                  variant="ghost" 
                  onClick={() => router.push('/login')}
                  className="font-medium"
                >
                  Login
                </Button>
                <Button 
                  onClick={() => router.push('/register')}
                  className="bg-gradient-to-r from-teal-500 to-cyan-600 hover:from-teal-600 hover:to-cyan-700 text-white font-medium"
                >
                  Sign Up
                </Button>
              </div>
            ) : (
              <div className="flex items-center space-x-3">
                {/* Placeholder to prevent layout shift */}
                <div className="w-32 h-9"></div>
              </div>
            )}
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <Button
              variant="ghost"
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className="p-2"
            >
              <svg className="h-6 w-6" fill="none" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" viewBox="0 0 24 24" stroke="currentColor">
                {isMobileMenuOpen ? (
                  <path d="M6 18L18 6M6 6l12 12" />
                ) : (
                  <path d="M4 6h16M4 12h16M4 18h16" />
                )}
              </svg>
            </Button>
          </div>
        </div>

        {/* Mobile menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden py-4 border-t">
            <div className="flex flex-col space-y-3">
              <Link href="/" className="text-gray-700 hover:text-teal-600 px-3 py-2">Home</Link>
              <Link href="/jobs" className="text-gray-700 hover:text-teal-600 px-3 py-2">Opportunities</Link>
              <Link href="/blog" className="text-gray-700 hover:text-teal-600 px-3 py-2">Health Hub</Link>
              <Link href="/contact-us" className="text-gray-700 hover:text-teal-600 px-3 py-2">Contact</Link>
              {isAuthenticated ? (
                <>
                  <Link href="/dashboard" className="text-gray-700 hover:text-teal-600 px-3 py-2">Dashboard</Link>
                  {isAdmin && <Link href="/admin" className="text-gray-700 hover:text-teal-600 px-3 py-2">Admin</Link>}
                  <button onClick={handleLogout} className="text-red-600 px-3 py-2 text-left">Logout</button>
                </>
              ) : (
                <>
                  <Link href="/login" className="text-gray-700 hover:text-teal-600 px-3 py-2">Login</Link>
                  <Link href="/register" className="bg-teal-600 text-white px-3 py-2 rounded text-center">Sign Up</Link>
                </>
              )}
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
